* {
    user-select: none;
}

.rulesContainer {
    display: flex;
    justify-content: center;
    animation: fade 2s;
    
}

.rulesContainerHeader {
    display: flex;
    justify-content: center;
    top: 100px;
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    user-select: none;
}

.rulesContainerItem {
    display: flex;
    justify-content: center;
    bottom: 400px;
}


.rulesContainerItem li img {
    color: white;
    background-color: rgba(0, 0, 0, 0.226);
    padding: 20px 20px;
    border-radius: 40px;
    transition: all 0.7s; 
}

.rulesContainerItem img:hover {
    transform: translateY(-20px);
    cursor: pointer;
   
    background-color: rgba(0, 0, 0, 0.479);
}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 100;
    }
}

.rulesContainerItem li {
    padding: 0 30px;
}