.nav {
  display: flex;
    align-items: center;
  
    padding: 0 30px;
 
    height: 50px;
    background-color: rgba(0, 0, 0, 0.233);
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    font-size: 15px;

   
 
    justify-content: space-around;
}

.nav ul {
  position: fixed;
}




ul {
  margin: 0;
  position: absolute;
}

ul li {
  display: inline;
}

a {
  width: 20%;
  padding: 0.5em 0.8em;
  
}

.item a {
  color: white;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
  text-decoration: none;
  font-size: 1.8rem;
  width: 1px;
  height: 1px;
  transition: all 0.5s;
  
}

.item a.activeLink {
  color: rgb(133, 135, 255);
}

.item a:hover {
  color: rgb(133, 135, 255);

}

.logo {
  width: 6%;
  display: flex;
}