.container {
    display: flex;
    justify-content: center;
    animation: fade 2s;
    
}

.container ul:not(.header) {
    top: 150px;
}

.container li img {
    color: white;
    background-color: rgba(0, 0, 0, 0.226);
    border-radius: 30px;
    transition: all 0.7s;
    margin: 0 0 0 30px;
    
}

.container li img:hover {
    transform: translateY(-20px);
    cursor: pointer;
    background-color: rgb(255, 255, 255);

}

.closeIcon {
    cursor: pointer;
    color: rgb(255, 255, 255);
    font-size: 50px;
    font-weight: bold;
    margin: 0 0 0 30px;
    transition: color 0.5s;
}

.closeIcon:hover {
    color: rgb(133, 135, 255);
}

.header {
    display: flex;
    justify-content: center;
    top: 50px;
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    user-select: none;

}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 100;
    }
}

