@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Oxygen:wght@300&display=swap');

::-webkit-scrollbar {
  width: 10px;
  background-color: rgb(6, 8, 44);
}

::-webkit-scrollbar-track {
  width: 25px;

}

::-webkit-scrollbar-thumb {
  background-image: linear-gradient(rgb(9, 8, 78), rgb(18, 20, 94));
  border-radius: 30px;
}


* {
  font-family: 'Nunito', sans-serif;
}

.appWrapper {
  max-width: 960px;
  margin: auto;
  display: flex;
  justify-content: center;
}

.mainDiv {
  position: absolute;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.233);
  position: fixed;
}