* {
    user-select: none;
}

.rulesTitle {
    text-align: center;
    color: white;
    font-weight: bold;
    font-size: 25px;
}

.rulesContainer {
    display: flex;
    justify-content: center;
    animation: fade 2s;
}

.rulesContainerItem {
    flex-wrap: wrap;
    height: 57em;
    display: flex;
    justify-content: center;
    color: white;
    max-width: 1000px;
    top:78px;
    background-color: rgba(0, 0, 0, 0.226);
    padding: 30px 30px;
    border-radius: 20px;
    font-size: 13.3px;
}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 100;
    }
}