.popupBox {
        position: fixed;
    background: #0000007e;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    animation: fade 0.5s backwards;
}

.box {
    display: flex;
    position: relative;
    margin: 0 auto;
    margin-top: calc(100vh - 85vh - 20px);
    padding: 30px 30px 30px 30px;
    flex-wrap: wrap;
    overflow: auto;
    color: white;
    background: linear-gradient(rgb(52, 52, 185), rgb(49, 49, 150));
    border-radius: 30px;
    max-width: 1000px;
    max-height: fit-content;
    box-shadow: 0 0 20px black;
    font-size: 20px;
    transition: opacity 1s;
}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 100;
    }
}