.rulesContainer {
    display: flex;
    justify-content: center;
    animation: fade 2s;
}

.rulesContainerItemLi {

    color: white;
    display: flex;
    justify-content: center;
}

.img img {
    animation: fade 2s;
    display: flex;
    justify-content: center;
    border-radius: 20px;
    width: 400px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    color: white;
    transition: transform 0.7s;

}

.text {
    animation: fade 2s;
   
}

.text p {
    animation: fade 2s;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    font-size: 25px;
}



.img img:hover {
    transform: translateY(-20px);
    cursor: pointer;
}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 100;
    }
}

.rulesContainerHeader {
    top: 100px;
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    user-select: none;
    position: fixed;
}

.rulesContainerItem {
    top: 120px;
    padding: 100px 100px;
}

.rulesContainerItem li img {
    display: flex;
    justify-content: center;
    border-radius: 20px;
    width: 400px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    color: white;
    transition: transform 0.7s;
}

.App {
    font-family: sans-serif;
    text-align: center;
}

.main {
    display: flex;
    flex-direction: column;

    padding: 10px;

}

.photo {
    width: 200px;


    margin-left: 20rem;

}

.img {
    height: 10px;

}

.rulesContainerItem img:hover {
    transform: translateY(-20px);
    cursor: pointer;
}

.rulesContainerItem li {
    padding: 0 30px;
}

.sliderBtn {
    width: 60px;
    height: 60px;
    border: none;
    border-radius: 50px;
    font-weight: bold;
    font-size: 30px;
    color: white;
    background-color: rgba(0, 0, 0, 0.322);
}

.sliderBtn:hover {
    background-color: rgb(255, 255, 255);
    color: black;
}


.serverContainer h1 {
    cursor: pointer;


   

}

.rulesContainerHeader {
    text-align: left;
    
}

.main{
    display: flex;
    justify-content: center;
}

.text a {
    cursor: pointer;
    animation: fade 2s;
    position: relative;
}

.serverContainer a.activeNav{
    color: rgb(81, 129, 231);
}