.footer {
    display: flex;
    align-items: center;
    margin-top: auto;
    padding: 0 30px;
    width: 100%;
    height: 50px;
    background-color: rgba(0, 0, 0, 0.233);
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    font-size: 15px;
    position: fixed;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    justify-content: space-around;
    text-align: center;
}

ul {
    margin: 0 5px;
    position: absolute;
}

ul li {
    display: inline;
}

a {
    width: 20%;
    padding: 0.5em 0.8em;
}

.item a {
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    text-decoration: none;
    font-size: 15px;
    width: 1px;
    height: 1px;
    transition: all 0.5s;
}

.item a.activeLink {
    color: rgb(133, 135, 255);
}

.item a:hover {
    color: rgb(133, 135, 255);

}

.logo {
    width: 6%;
    display: flex;
}