.rulesContainer {
    animation: fade 2s;
    position: absolute;
    top: -32px;
}

.aboutUs {
    position: absolute;
    top: 100px;
    color: white;
    font-size: 35px;
    text-align: center;
    padding: 10px 10px;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.226);
    width: 960px;
}

.aboutUsBlock {
    padding: 10px 10px;
    display: flex;
    align-items: center;
    font-size: 20px;
    text-align: left;
}

.news {
    position: absolute;
    top: 540px;
    color: white;
    font-size: 35px;
    text-align: center;
    padding: 10px 10px;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.226);
    width: 960px;

}

.newsBlock {
    display: flex;
    align-items: center;

}

.bg img {
    border-radius: 20px;
    width: 200px;

}

.bg {
    padding: 10px 10px;

}

.newsText {
    font-size: 20px;

}

.play {
    position: absolute;
    top: 460px;
    width: 300px;
    text-align: center;

}

.play a {
    text-decoration: none;
    color: white;
}

.rulesContainerHeader {
    top: 60%;
    color: white;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.616);
    user-select: none;
}

.rulesContainerItem {
    display: flex;
    justify-content: center;
    top: 60%;
}

.rulesContainerItem .play {
    font-size: 30px;
    font-weight: bold;
    color: white;
    background-color: rgba(0, 0, 0, 0.226);
    padding: 10px 10px;
    border-radius: 20px;
    transition: all 0.7s;
}

.rulesContainerItem .play:hover {
    transform: translateY(-20px);
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.479);
}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 100;
    }
}

.rulesContainerItem {
    padding: 0 30px;
}